import Logo from "../../images/logo.png";
import "./Navbar.css"
import React, { useState, useEffect } from "react";
import Grid from "../../images/grid.png";
import { Link } from "react-router-dom";
import { HashLink } from 'react-router-hash-link';
import { auth, logout } from "../../firebase-config";
import { useAuthState } from 'react-firebase-hooks/auth';
import { useNavigate } from "react-router-dom";



const Navbar = () => {
  // const [isAuth, setIsAuth] = useState(localStorage.getItem("isAuth"));

  const [user, loading, error] = useAuthState(auth);
  let navigate = useNavigate();
  // const signUserOut = () => {
   
  //     window.location.pathname = "/login";
  //   });
  // };
  useEffect(() => {
    if (loading) return;
    if (!user) return navigate("/");
  }, [user, loading]);


  const [matches, setMatches] = useState(
    window.matchMedia("(min-width: 1024px)").matches
  );
  useEffect(() => {
    window
      .matchMedia("(min-width: 1024px)")
      .addEventListener("change", (e) => setMatches(e.matches));
  }, [user]);

  const [show, setShow] = useState(false);

  const linkClick = () => {
    show && setShow(false);
    console.log(show);
  };
  const imageClick = () => {
    show ? setShow(false) : setShow(true);
    console.log(show);
  };
  return (

    <div className="navbar" id="navbar">
      {(() => {
        if (matches) {
          return (
            <div className="navbar_content">

              <Link to="/">
                <img src={Logo} alt="" />
              </Link>

              <div className="nav_links">
                 {!user ? (
                 <ul>
                      <li>
                      <HashLink smooth to="/#howItWorks"> How it works</HashLink>
                      </li>
                      <li>
                      <HashLink smooth to="/#whoItWorksFor">Who is it for</HashLink>
                      </li>
                      <li>
                        <Link to="/faq">FAQ</Link>
                      </li>
                      <li>
                        <Link to="/blog">Blog</Link>
                      </li>
                      <li><Link to="/login"> Login</Link></li>
                    </ul>
                  ) : (
                    <ul className="logout">
                      <li><a onClick={logout}> Log Out</a></li>
                      </ul>
                  )}
              </div>
            </div>
          );
        } else {
          return (
            <div className="navbar_content">

              <Link to="/" onClick={() => linkClick()}>
                <img src={Logo} alt="" />
              </Link>


              <img
                src={Grid}
                className="dropdown"
                onClick={() => imageClick()}
                alt=""
              />
              <div className="nav_links" style={{ display: show && "block" }}>
                <ul className="mobile_ul">
                  {!user ? (
                    <>
                      <li>
                      <HashLink smooth to="/#howItWorks" onClick={() => linkClick()}>How it works</HashLink>
                      </li>
                      <li>                        
                      <HashLink smooth to="/#whoItWorksFor" onClick={() => linkClick()}>Who is it for</HashLink>
                      </li>
                      <li>
                        <Link to="/faq" onClick={() => linkClick()}>FAQ</Link>
                      </li>
                      <li>
                        <Link to="/blog" onClick={() => linkClick()}>Blog</Link>
                      </li>

                      <li><Link to="/login" onClick={() => linkClick()}> Login</Link></li>
                    </>
                  ) : (
                    <>
                      {/* <li><Link to="/createpost" onClick={() => linkClick()}> Create Post </Link></li>
                      <li><Link to="/blogadmin" onClick={() => linkClick()}>Admin Blog </Link></li> */}
                      <li><button onClick={() => {linkClick()
                        logout()}}> Log Out</button></li>
                    </>
                  )}
                </ul>
              </div>
            </div>
          );
        }
      })()}
    </div>

    // }

  );
};

export default Navbar;
